.App {
  text-align: center;
  background: linear-gradient(to top right, #060628, #1f245a, #682359);
  height: 100vh;
}

.pageTitle {
  display: block;
  padding-top: 10px;
  color: gold;
  font-family: "Permanent Marker", cursive;
  font-size: 45px;
}

.fileSize {
  margin: 0;
  padding-bottom: 10px;
  color: gold;
  font-family: "Permanent Marker", cursive;
  font-size: 25px;
}

.audio-react-recorder {
  padding: 10px;
}

.audio-react-recorder__canvas {
  background: black;
  border: 15px solid;
  border-image-slice: 1;
  border-image-source: linear-gradient(to left, #743ad5, #d53a9d);
}

.recorder-btn {
  padding: 10px;
}

.recorder-btn button {
  margin: 7px 10px;
}

.recorder-btn button i {
  font-size: 20px;
}

.w-btn-neon2 {
  position: relative;
  border: none;
  min-width: 50px;
  min-height: 50px;
  background: linear-gradient(315deg, #1fd1f9 0%, #b621fe 74%);
  box-shadow: 0 4px 15px 0 rgba(126, 52, 161, 0.75);
  border-radius: 1000px;
  color: white;
  cursor: pointer;

  font-weight: 700;
  transition: 0.3s;
}

.w-btn-neon2:hover {
  transform: scale(1.2);
}

.w-btn-neon2:hover::after {
  content: "";
  width: 30px;
  height: 30px;
  border-radius: 100%;
  border: 3px solid gold;
  position: absolute;
  z-index: -1;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  animation: ring 1.5s infinite;
}

@keyframes ring {
  0% {
    width: 30px;
    height: 30px;
    opacity: 1;
  }
  100% {
    width: 70px;
    height: 70px;
    opacity: 0;
  }
}

.btn-active {
  color: gold;
}

.uploadFile {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 15px;
}

.uploadIcon {
  font-size: 40px;
}

.uploadBox p {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 20px;
}

.btn {
  display: inline-block;
  margin: 0 4px;
  font-size: inherit;
  line-height: 1.42;
  padding: 10px 15px;
  font-weight: 600;
  font-size: 15px;
  border-width: 1px;
  border-style: solid;
  background: transparent;
  border-radius: 0.25em;
  cursor: pointer;
  font-family: "Booster Next FY", "Avenir Next", Avenir, sans-serif;
  user-select: none;
  vertical-align: bottom;
  background-color: transparent;
  border-width: 1px;
  border-style: solid;
  box-shadow: 0 1.31em 0 0 rgba(255, 255, 255, 0.1) inset;
  transition: box-shadow 0.3s cubic-bezier(0.215, 0.61, 0.355, 1),
    border-color 0.3s cubic-bezier(0.215, 0.61, 0.355, 1);
}
.btn.btn-disabled {
  cursor: not-allowed;
  opacity: 0.8;
}
.btn.btn-disabled:active {
  opacity: 0.6;
}
.btn:active {
  box-shadow: 0 1.31em 0.2em 0 rgba(0, 0, 0, 0.08) inset;
  transition: box-shadow 0.1s;
}

.uploadBtn {
  background-color: #991be2;
  background-image: linear-gradient(-45deg, #991be2, #640eef);
  color: #e6eaef;
  border-top-color: #c275ee;
  border-left-color: #a635e7;
  border-right-color: #a635e7;
  border-bottom-color: #5b1087;
}

.uploadBtn:active {
  color: #5b1087;
  text-shadow: 0 1px 0 rgba(255, 255, 255, 0.2);
  background-color: #931ad9;
  background-image: linear-gradient(-45deg, #931ad9, #600ee5);
  border-top-color: #5b1087;
  border-left-color: #8618c7;
  border-right-color: #8618c7;
  border-bottom-color: #8618c7;
}

.uploadBtn:hover {
  background-image: linear-gradient(-45deg, #9d23e5, #6a16f1);
}

.parseBtn {
  background-color: #ff006a;
  background-image: linear-gradient(-45deg, #ff006a, #ff00aa);
  color: #e6eaef;
  border-top-color: #ff66a6;
  border-left-color: #ff1f7c;
  border-right-color: #ff1f7c;
  border-bottom-color: #990040;
}

.parseBtn:active {
  color: #990040;
  text-shadow: 0 1px 0 rgba(255, 255, 255, 0.2);
  background-color: #f50066;
  background-image: linear-gradient(-45deg, #f50066, #f500a3);
  border-top-color: #990040;
  border-left-color: #e0005d;
  border-right-color: #e0005d;
  border-bottom-color: #e0005d;
}
.parseBtn:hover {
  background-image: linear-gradient(-45deg, #ff0a70, #ff0aad);
}

.warning {
  display: flex;
  justify-content: center;
  align-items: center;
}

.warning i {
  font-size: 25px;
}
